import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import LargeGoldIcon from '../../images/LargeGold.png'
import LargeSilverIcon from '../../images/LargeSilver.png'
import LargeCopperIcon from '../../images/LargeCopper.png'

export default class Standing extends React.Component {
    render() {
        let image;
        switch (this.props.Place) {
            case 1:
                image = <img src={LargeGoldIcon} alt={this.props.Place}></img>
                break;
            case 2:
                image = <img src={LargeSilverIcon} alt={this.props.Place}></img>
                break;
            case 3:
                image = <img src={LargeCopperIcon} alt={this.props.Place}></img>
                break;
            default:
                break;
        }
        return (
            <Card className='mb-1'>
                <Row>
                    <Col sm={2} className='text-center my-auto medal'>
                        {image}
                    </Col>
                    <Col sm={4} className='my-auto'>{this.props.Name}</Col>
                    <Col sm={2} className='my-auto text-center'>{this.props.FreeCompany}</Col>
                    <Col sm={2} className='my-auto text-right'>{this.props.Score}</Col>
                    <Col sm={2} className='my-auto'>
                        <img
                            className='float-right img-thumbnail'
                            src={this.props.Avatar}
                            alt={this.props.Name}
                            style={{ width: '96px', height: '96px' }}
                        />
                    </Col>
                </Row>
            </Card>
        )
    }
}